/* Шрифт */
@font-face {    
	font-family: "Gropled"; 
	src:url("./Gropled-Bold.otf") format("opentype"); 
  }
  
  /* Основные стили навигации */
  .nav {
	padding: 20px 0;
	background-color: #000;
	border-bottom: 1px solid #333;
	color: #fff;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 1000;
	transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
  }
  
  /* Анимация навбара */
  .nav--visible {
	transform: translateY(0);
	opacity: 1;
  }
  .nav--hidden {
	transform: translateY(-100%);
	opacity: 0;
  }
  
  /* Стиль ряда навигации */
  .nav-row {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 30px;
	position: relative;
	z-index: 5;
  }
  
  /* Кнопка темного режима */
  .btn-dark-mode {
	position: fixed;
	top: 10px;
	right: 10px;
	z-index: 20;
	cursor: pointer;
  }
  
  /* Логотип */
  .logo {
	color: #fff;
	font-size: 24px;
	font-family: 'Gropled', sans-serif;
	position: absolute;
	z-index: 1;
	text-decoration: none;
	white-space: nowrap;
  }
  
  /* Положение логотипа на ПК */
@media (min-width: 769px) {
	.logo {
	  left: 20px;
	  top: 50%;
	  transform: translateY(-50%);
	}
  }
  
  /* Положение логотипа на мобильных устройствах */
  @media (max-width: 768px) {
	.logo {
	  position: static;
	  font-size: 30px;
	  text-align: center;
	  margin: 0 auto;
	  display: block;
	  top: auto;
	  left: auto;
	  transform: none;
	}
  }
  
  /* Анимация свечения и движения текста */
  @keyframes glow {
	0% { text-shadow: 0 0 0 rgba(255, 255, 255, 0); }
	100% { text-shadow: 0 0 10px rgba(255, 255, 255, 0.7); }
  }
  @keyframes slide-up-blur {
	0% { transform: translateY(0); opacity: 1; filter: blur(0); }
	100% { transform: translateY(-100%); opacity: 0; filter: blur(3px); }
  }
  @keyframes slide-down-blur {
	0% { transform: translateY(100%); opacity: 0; filter: blur(3px); }
	100% { transform: translateY(0); opacity: 1; filter: blur(0); }
  }
  
  /* Текст перед анимацией */
  .logo::before,
  .logo::after {
	content: 'Донецкие аэрозоли';
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	display: block;
	pointer-events: none;
  }
  .logo::before {
	opacity: 1;
	transform: translateY(0);
	filter: blur(3px);
  }
  .logo::after {
	transform: translateY(100%);
	opacity: 0;
	filter: blur(0);
  }
  
  /* Эффекты при наведении */
  .logo:hover::before {
	animation: slide-up-blur 0.5s cubic-bezier(0.42, 0, 0.58, 1) forwards, glow 0.3s ease-out;
  }
  .logo:hover::after {
	animation: slide-down-blur 0.5s cubic-bezier(0.42, 0, 0.58, 1) forwards;
  }
  .logo:not(:hover)::before {
	animation: slide-down-blur 0.3s cubic-bezier(0.42, 0, 0.58, 1) forwards, glow 0.3s ease-out;
  }
  .logo:not(:hover)::after {
	animation: slide-up-blur 0.3s cubic-bezier(0.42, 0, 0.58, 1) forwards;
  }
  
  /* Стили для навигационных ссылок */
 /* Стили для навигационных ссылок */
.nav-list {
	display: flex;
	flex-wrap: nowrap;
	align-items: center;
	column-gap: 40px;
	font-size: 16px;
	font-weight: 500;
	font-family: 'Gropled';
	z-index: 5;
  }
  .nav-list__link {
	color: #fff;
	transition: opacity 0.2s ease-in;
	position: relative;
	cursor: pointer;
	z-index: 10;
	display: inline-block;
	padding: 0;
  }
  .nav-list__link::before {
	content: '';
	position: absolute;
	left: 0;
	bottom: -3px;
	width: 0;
	height: 2px;
	background-color: #fff;
	transition: width 0.3s ease-in-out;
  }
  .nav-list__link:hover::before {
	width: 100%;
  }
  .nav-list__link--active::before {
	width: 100%;
  }
  
  /* Адаптивные стили */
  @media (max-width: 768px) {
	.nav-row {
	  flex-direction: column; /* Изменение направления элементов на вертикальное */
	  align-items: center;
	}
	.nav-list {
	  flex-direction: column; /* Ссылки в навигации выстраиваются друг под другом */
	  align-items: center;
	  width: 100%;
	  padding: 8px 0;
	  row-gap: 20px; /* Промежуток между ссылками по вертикали */
	}
	.nav-list__link {
	  font-size: 22px; /* Увеличение размера шрифта для лучшей читабельности */
	}
  }
  