.section {
    padding: 20px 0;
    margin-top: 80px; /* Отступ сверху для обычного экрана */
}

@media (max-width: 768px) {
    .section {
        margin-top: 220px; /* Увеличенный отступ сверху для экранов меньше 768px */
    }
}

/* Остальной код остается без изменений */

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.title-1 {
    margin-bottom: 60px;
    font-size: 70px;
    font-weight: 700;
    line-height: 1.3;
    color: var(--title-1);
    font-family: 'Raleway';
    text-align: center;
}

.project-details {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 40px;
    flex-direction: row;
}

.project-details__cover {
    max-width: 40%;
    border-radius: 20px;
}

.project-details__desc {
    max-width: 50%;
    font-size: 18px;
    line-height: 1.6;
    color: var(--text-color);
    text-align: left;
}

.button-container {
    margin-top: 20px;
}

.btn-outline-3 {
    display: inline-flex;
    align-items: center;
    padding: 10px 20px;
    border: 1px solid var(--title-1);
    border-radius: 4px;
    color: var(--title-1);
    text-decoration: none;
    background: transparent;
    transition: background-color 0.3s ease, color 0.3s ease;
    font-family: 'Raleway';
    font-size: 18px;
    text-align: center;
}

.btn-outline-3:hover {
    background-color: #333;
    color: #fff;
}

@media (max-width: 768px) {
    .project-details {
        flex-direction: column;
        align-items: center;
    }

    .project-details__cover {
        max-width: 80%;
        margin-bottom: 20px;
    }

    .project-details__desc {
        max-width: 90%;
        margin-top: 0;
        text-align: center;
    }

    .title-1 {
        font-size: 50px; /* Уменьшаем размер заголовка на мобильных */
        margin-top: 0;
    }
}
